@mobile: 320px;
@mobile-container: 260px;
@menu-shadow: 0 1px 0 rgba(236, 236, 236, 1), inset 0 1px 0  rgba(236, 236, 236, 1);
@modal-shadow: 0 5px 10px 0 fade(#000101, 25%);

.visually-hidden {
  position: absolute;
  overflow: hidden;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  margin: -1px;
  clip: rect(0 0 0 0);
}

.open-sans() {
  font-family: "Open-sans", "Arial", sans-serif;
  font-weight: 400;
}

.open-sans-bold() {
  font-family: "Open-sans", "Arial", sans-serif;
  font-weight: 700;
}

.align-center() {
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}
