.advantages {
  display: flex;
  flex-direction: column;
}

.advantages__wrapper {
  padding: 0 30px;
  margin-bottom: 58px;
}

.advantages__title {
  margin: 0;
  margin-bottom: 28px;
}

.advantages__item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 90px;

  &:not(:last-child) {
    margin-bottom: 34px;
  }
}

.advantages__description {
  .open-sans-bold();
  font-size: 17px;
  line-height: 20px;
  margin: 0;
}

.advantages__icon {
  fill: #62d0ba;
}

.advantages__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
